"use client";

import { useState, useEffect } from "react";
import { useParams, useRouter } from "next/navigation";
import Image from "next/image";
import Link from "next/link";

export default function BlogHome() {
  const [blogData, setBlogData] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);
  const { interna } = useParams();
  const router = useRouter();
  const [filteredArticles, setFilteredArticles] = useState([]);
  const [selectedCategory, setSelectedCategory] = useState(null);

  useEffect(() => {
    if (interna === "1") {
      router.push("/");
      return; // Detener el fetch
    }

    const fetchBlogData = async () => {
      try {
        const response = await fetch(`${process.env.NEXT_PUBLIC_API_URL}/json/node/${interna}`, { 
          method: "GET" 
        });
        
        if (!response.ok) {
          throw new Error(`Error en la respuesta: ${response.status}`);
        }

        const data = await response.json();
        setBlogData(Array.isArray(data) ? data : [data]);
      } catch (err) {
        console.error("Error al obtener los datos del blog:", err);
        setError(err.message);
      } finally {
        setIsLoading(false);
      }
    };

    fetchBlogData();
  }, [interna, router]);

  // Procesar los datos del blog para obtener los artículos filtrados
  // useEffect(() => {
  //   if (blogData && blogData.length > 0) {

  //     // Aplicar filtro por categoría si hay una seleccionada
  //     if (selectedCategory) {
  //       setFilteredArticles(articulos.filter(art => art.categoria === selectedCategory));
  //     } else {
  //       setFilteredArticles(articulos);
  //     }
  //   } else {
  //     setFilteredArticles([]);
  //   }
  // }, [blogData, selectedCategory]);

  // Función para manejar la selección de categoría
  const handleCategorySelect = (category) => {
    setSelectedCategory(category === selectedCategory ? null : category);
  };

  // Obtener categorías únicas
  const uniqueCategories = blogData && blogData.length > 0
    ? Array.from(new Set(blogData.map(item => 
        item?.fields?.field_categories?.[0]?.label || 'Sin categoría')))
    : [];

  // Obtener etiquetas únicas
  const uniqueTags = blogData && blogData.length > 0
    ? Array.from(new Set(blogData.flatMap(item => 
        item?.fields?.field_labels?.map(label => label.label) || [])))
    : [];

  if (isLoading) return <div>Cargando...</div>;
  if (error) return <div>Error: {error}</div>;

  return (
    <div className="principal-blog">
      <div className="principal-blog-header">
        <div className="principal-blog-header-items">
          <div className="principal-blog-header-title">
            <h1 className="principal-blog-title">Blog</h1>
            <h2 className="principal-blog-subtitle">
              Nuestro conocimiento en tus manos
            </h2>
          </div>
          <div className="principal-blog-breadcrumb">
            <Link href="/">Inicio</Link> / Blog
          </div>
        </div>
      </div>
      <div className="principal-blog-labels-and-articles">
        <div className="principal-blog-labels">
          <div className="principal-blog-labels-categories">
            <h1 className="principal-blog-labels-categories-title">Categorías</h1>
            <div className="principal-blog-labels-categories-items">
              {uniqueCategories.map((category, index) => (
                <a 
                  className={`principal-blog-label-tag-item ${selectedCategory === category ? 'active' : ''}`} 
                  key={index}
                  onClick={() => handleCategorySelect(category)}
                  style={{ cursor: 'pointer' }}
                >
                  {category}
                </a> 
              ))}
              <div className="principal-blog-label-categories-item-all">
                <a
                  className="principal-blog-label-categories-link"
                  onClick={() => setSelectedCategory(null)}
                  style={{ cursor: 'pointer' }}
                >
                  Todas las categorías
                </a>
              </div>
            </div>
          </div>
          <div className="principal-blog-labels-tag">
            <h1 className="principal-blog-labels-tag-title">Etiquetas</h1>
            <div className="principal-blog-labels-tag-items">
              {uniqueTags.map((tag, index) => (
                <div className="principal-blog-label-tag-item" key={index}>
                  <a className="principal-blog-label-tag-link">
                    {tag}
                  </a>
                </div>
              ))}
            </div>
          </div>
        </div>
        <div className="principal-blog-articles">
          {filteredArticles.length === 0 && (
            <div className="principal-blog-no-articles">
              <p>No hay artículos disponibles para esta categoría.</p>
            </div>
          )}
          {filteredArticles.map((articulo) => (
            <Link 
              href={articulo.slug} 
              key={articulo.id} 
              className="principal-blog-article"
            >
              <div className="principal-blog-article-title">
                {articulo.titulo}
              </div>
              <div className="principal-blog-article-image">
                <Image
                  className="principal-blog-image"
                  src={articulo.imagen}
                  alt={articulo.titulo}
                  width={300}
                  height={150}
                  priority
                />
                <div className="principal-blog-article-date">
                  <p>{articulo.fecha}</p>
                </div>
              </div>
              <div className="principal-blog-article-label">
                <span>{articulo.categoria}</span>
              </div>
              <div className="principal-blog-article-description">
                <p>{articulo.descripcion}</p>
              </div>
              <div className="principal-blog-article-see-more">
                <span>Ver más</span>
              </div>
            </Link>
          ))}
        </div>
      </div>
    </div>
  );
}